import React from 'react';
import {BackButton, Toolbar} from "react-onsenui";
import {getLang} from "@web3game/lang";
import {popPage} from "@web3game/common/utils/navigatorHelper";

type HeaderProps = {
    title?: string;
    hasBack?: boolean;
    backText?: string;
    onBack?: () => void;
    renderRightButton?: () => (React.ReactElement | null);
};

export function Header(
    {
        backText = getLang().BACK,
        hasBack = true,
        onBack = () => popPage(),
        title = '',
        renderRightButton = () => null,
    }: HeaderProps,
) {
    return (
        <Toolbar>
            <div className="left">
                {!hasBack ? null : (
                    <BackButton onClick={onBack}>
                        {backText}
                    </BackButton>
                )}
            </div>
            <div className="center">
                {title}
            </div>
            <div className="right">
                {renderRightButton()}
            </div>
        </Toolbar>
    );
}

export const createHeaderRender = (
    title: string,
    otherProps: HeaderProps = {},
) => () => (
    <Header title={title} {...otherProps} />
);

export const createNoBackHeaderRender = (
    title: string,
) => createHeaderRender(title, { hasBack: false });
