import {EventEmitter as EventEmitterBase} from "events";


class Collection {
    private collection: (() => void)[] = [];

    /** @returns {Collection} */
    public push(...fn: (() => void)[]) {
        this.collection.push(...fn);
        return this;
    }

    public removeAll() {
        this.collection.forEach(fn => fn());
        this.collection = [];
    }

    public getCollection() {
        return this.collection;
    }
}

export class EventEmitter extends EventEmitterBase {

    protected EventKeys = {};

    private removeEventListeners: (() => void)[] = [];

    public constructor(eventKeys?: object) {
        super();

        /** @field */
        this.EventKeys = { ...eventKeys };

        this.setMaxListeners(9999);
    }

    // @ts-ignore
    public on(eventName: string, fn: (() => void) | any) {
        super.on(eventName, fn);

        const removeFn = () => {
            this.removeListener(eventName, fn);
            this.removeEventListeners = this.removeEventListeners.filter(o => o !== removeFn);
        };

        removeFn.eventName = eventName;

        if (!this.removeEventListeners) {
            this.removeEventListeners = [];
        }

        this.removeEventListeners.push(removeFn);

        return removeFn;
    }

    public one(eventName: string, fn: (...args: any[]) => void) {
        const dispose = this.on(eventName, (...args: any[]) => {
            fn(...args);
            dispose();
        });

        return dispose;
    }

    public removeByName(name: string) {
        this.removeEventListeners.forEach((fn) => {
            if ((fn as any).eventName === name) fn();
        });

        this.removeEventListeners = this.removeEventListeners.filter(
            o => (o as any).eventName !== name,
        );
    }

    public removeAll() {
        this.removeEventListeners.forEach(fn => fn());
        this.removeEventListeners = [];
    }

    /** @returns Collection */
    public createCollection() {
        return new Collection();
    }

    public listenToMany(...listeners: (() => void)[]) {
        const collection = this.createCollection();
        collection.getCollection().push(...listeners);
        return () => collection.removeAll();
    }
}

export default EventEmitter;
