import React, {useCallback, useState} from 'react';
import {getLang} from "@web3game/lang";
import {Button, Input, List, ListItem, ListTitle, Page, Icon, Modal, Toast} from "react-onsenui";
import {withAPI} from "@web3game/common/components/withAPI";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import Clipboard from 'react-clipboard.js';
import {InvitationInfo} from "@web3game/typings/home";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {watchAccountChange} from "@web3game/common/utils/watchAccountChange";
import {createHeaderRender} from "../Header";
import {Block} from "../common/Block";

// https://www.npmjs.com/package/qrcode

export type InvitationPageProps = InvitationInfo & {
    onSetInvitor: (invitor: string) => void;
};

export function InvitationPagePure(
    {
        code,
        myInviter,
        invitationList,
        onSetInvitor,
    }: InvitationPageProps,
) {
    const renderFixed = createHeaderRender(getLang().HOME_PAGE.INVITATION);
    const [invitatorValue, setInvitatorValue] = useState('');
    const [toastText, setToastText] = useState('');
    const submitInvitator = () => {
        onSetInvitor(invitatorValue);
    };
    const showToast = (text: string) => {
        setToastText(text);
        setTimeout(() => setToastText(''), 1000);
    };
    const renderInvitorForm = () => {
        return (
            <Block noMargin>
                <div className="inputGroup">
                    <Input onChange={e => setInvitatorValue(e.target.value)} />
                    <Button style={{ width: 190 }} onClick={submitInvitator}>
                        {getLang().HOME_PAGE.SET_INVITOR}
                    </Button>
                </div>
            </Block>
        );
    };

    const renderCode = () => {
        return (
            <Clipboard
                component="a"
                data-clipboard-text={code}
            >
                <span onClick={() => showToast(`${getLang().COPIED} ${code}`)}>
                    {code}
                    {"  "}
                    <Icon
                        icon="md-copy"
                    />
                </span>
            </Clipboard>
        );
    }


    return (
        <Page renderFixed={renderFixed}>
            <Block noMargin oneHorizontalPadding transparent>
                <Block>
                    {getLang().HOME_PAGE.MY_INVITATION_CODE}
                    {"  "}
                    {code && code !== '' && renderCode()}
                    <Toast isOpen={!!toastText}>
                        <span>{toastText}</span>
                    </Toast>
                </Block>

                <Block noMargin oneMarginBottom>
                    {getLang().HOME_PAGE.MY_INVITOR}
                    {"   "}
                    <span style={{fontSize: '12px'}}>{myInviter && myInviter !== '0x0000000000000000000000000000000000000000' ? myInviter : ''}</span>
                    {(!myInviter || myInviter === '0x0000000000000000000000000000000000000000') && renderInvitorForm()}
                </Block>

                <Block noMargin>
                    <div className="textGrey">
                        <ListTitle style={{ fontSize: 16 }}>
                            {getLang().HOME_PAGE.MY_GROUP}
                        </ListTitle>
                        <List
                            style={{fontSize: "12px"}}
                            dataSource={invitationList}
                            renderRow={(item: any, index: number) => (
                                <ListItem key={index}>
                                    <span>{index + 1}.</span>
                                    <span style={{paddingLeft: "7px"}}>{item}</span>
                                </ListItem>
                            )}
                        />
                    </div>
                </Block>
            </Block>

        </Page>
    );
}

export const InvitationPage = compose<InvitationPageProps>(
    hot(module),
    React.memo,
    watchAccountChange,
    withAPI(async ({ updateDataVersion }) => {
        const homeAPI = await getHomeAPI();
        const {
            invitationList,
            myInviter,
            code,
        } = await homeAPI.getInvitationResponse();

        const onSetInvitor = (invitor: string) => {
            (async () => {
                try {
                    await homeAPI.setInvitor(invitor);
                    updateDataVersion();
                } catch (e) {
                    alert(e.message);
                }
            })();
        };

        return {
            invitationList,
            myInviter,
            code,
            onSetInvitor,
        };
    }),
)(InvitationPagePure);

export default InvitationPage;
