import React, {
    ComponentType,
    FunctionComponent,
    MemoExoticComponent,
    NamedExoticComponent,
    useEffect,
    useState,
} from "react";
import _, {values} from 'lodash';

const defaultGetAPIDependencies = (props: any) => values(props);
type APIDependenciesGetter<P> = (props: P) => any[];
type APIFetcher<P> = (props: P) => Promise<object>

/* eslint @typescript-eslint/indent:0 */
type WithAPIComponentType<P> = FunctionComponent<P>
    |ComponentType<P>
    | MemoExoticComponent<ComponentType<P>>
    | NamedExoticComponent<P>;

export function withAPI<P extends object>(
    apiFetchers: APIFetcher<Partial<P>>,
    getApiDependencies: APIDependenciesGetter<P> = defaultGetAPIDependencies,
) {
    return (Component: WithAPIComponentType<P>) => React.memo<P>(
        (props: P) => {
            const [apiValues, setApiValues] = useState({});
            const dependencies = getApiDependencies(props);
            const dataVersion = _.get(props, 'dataVersion');
            if (dataVersion) {
                dependencies.push(dataVersion);
            }

            useEffect(
                () => {
                    (async () => {
                        const responseValues = await apiFetchers(props);
                        setApiValues(responseValues);
                    })();
                },
                dependencies,
            );

            const C = Component as any;

            return (
                <C
                    {...props}
                    {...apiValues}
                />
            );
        },
    );
}
