import _ from 'lodash';
import {Navigator, PageTransitionOptions} from "react-onsenui";
import {emitBack} from "./NavigatorEvents";


const historyStack: object[] = [];

export const hasHistory = () => historyStack.length > 0;

const HashSplitter = '|';

function pushHash(hash: string) {
    const currentHash = document.location.hash;
    document.location.hash = `${currentHash}${currentHash ? HashSplitter : ''}${hash}`;
}

export async function pushPage(
    navigator: Navigator,
    router: any,
    options: PageTransitionOptions = {
    },
) {
    const routerName = router.key;
    pushHash(routerName);
    historyStack.push({ navigator, router, options, routerName });
    await navigator.pushPage(router, options);
}

export function popPage() {
    window.history.back();
}

function popPageInner(componentName: string) {
    const { navigator, routerName } = _.last(historyStack) as any;

    historyStack.pop();

    if (routerName === componentName) {
        navigator.popPage();
    }
}

// handle history back
window.addEventListener('hashchange', (event) => {
    const { newURL, oldURL } = event;
    if (newURL.length < oldURL.length) {
        const componentName = oldURL.replace(newURL, '').replace('#', '');
        emitBack(componentName);
        popPageInner(componentName);
    }
});

if (window.location.hash) {
    window.history.replaceState(null, '', '#');
}


