import EventEmitter from "./EventEmitter";

const EventKeys = {
    back: 'back',
    push: 'push',
};

const NavigatorEvents = new EventEmitter();

export function emitBack(componentName: string) {
    NavigatorEvents.emit(EventKeys.back, componentName);
}

export function onBack(fn: (componentName: string) => void) {
    return NavigatorEvents.on(EventKeys.back, fn);
}

export function emitPush(componentName: string) {
    NavigatorEvents.emit(EventKeys.push, componentName);
}

export function onPush(fn: (componentName: string) => void) {
    return NavigatorEvents.on(EventKeys.push, fn);
}

