import React from 'react';
import {inject, observer} from "mobx-react";
import {AccountStore} from "../stores/AccountStore";
import {compose} from "./compose";

type RC<P> = React.ComponentType<P> | React.FunctionComponent<P> ;


type WatchLocaleInjectedProps = { accountStore: AccountStore };

export function injectMainAccountData<P>(Component: RC<P>) {
    return inject(
        (
            {
                accountStore,
            }: WatchLocaleInjectedProps,
        ) => (
            {
                locale: accountStore.locale,
                dataVersion: accountStore.dataVersion,
                updateDataVersion: () => accountStore.updateDataVersion(),
            }
        ),
    )(Component);
}

export function watchAccountChange<P>(Component: RC<P>) {
    return compose<P>(
        observer,
        injectMainAccountData,
    )(Component);
}


